import React from "react";
import styled from "styled-components";

function IntroduceBookBox() {
  const SectionBookBox = styled.section`
    padding-top: 28px;
    box-sizing: border-box;
    header {
      width: 1024px;
      margin: 0 auto;
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
    }
  `;
  const FlexUlBox = styled.ul`
    display: flex;
    justify-content: center;
    li {
      box-sizing: border-box;
      a {
      }
    }
  `;
  const Figure = styled.figure`
    img {
      width: 162px;
      height: 244px;
      margin: 0 auto;
      background-color: #efefef;
      border-radius: 10px;
      display: block;
    }
  `;
  return (
    <SectionBookBox className="section_bookbox-audiobook">
      <header>
        <h3>
          <img src="images/introduce/bookbox_title.png" alt="북크박스"></img>
        </h3>
        <p>
          <strong>진짜 바쁠 땐 고전부터 신간까지 요약한</strong>
        </p>
        <p>지식인들의 10분 북인사이트</p>
      </header>
      <FlexUlBox>
        <li>
          <a href="#">
            <Figure>
              <img
                src="images/introduce/8_1.png"
                alt="걸어서 출퇴근하는 배우"
              ></img>
              <figcaption>
                <p>걷는사람,하정우</p>
                <p>하정우</p>
              </figcaption>
            </Figure>
          </a>
        </li>
        <li>
          <a href="#">
            <Figure>
              <img
                src="images/introduce/8_1.png"
                alt="걸어서 출퇴근하는 배우"
              ></img>
              <figcaption>
                <p>걷는사람,하정우</p>
                <p>하정우</p>
              </figcaption>
            </Figure>
          </a>
        </li>
        <li>
          <a href="#">
            <Figure>
              <img
                src="images/introduce/8_1.png"
                alt="걸어서 출퇴근하는 배우"
              ></img>
              <figcaption>
                <p>걷는사람,하정우</p>
                <p>하정우</p>
              </figcaption>
            </Figure>
          </a>
        </li>
      </FlexUlBox>
      <a href="#">
        <button type="button">무료듣기</button>
      </a>
    </SectionBookBox>
  );
}
export default IntroduceBookBox;
